* {
  font-family: "Google Sans", sans-serif;
  transition: all 0.2s ease-in-out;
  object-position: center;
  box-sizing: border-box;
  object-fit: cover;
  padding: 0px;
  margin: 0px;
}

:root {
  --body: rgba(241, 241, 241, 0.5);
  --font-gray: rgba(62, 62, 62, 0.5);
  --border-color: rgba(0, 0, 0, 0.1);

  --primary-color: #1fa2dd;

  --white: #ffffff;
  --black: #3e3e3e;
  --red: #ff3d00;

  --facebook: #2e89ff;
  --codepen: #3b3b3b;
  --twitter: #03a9f4;
  --github: #000000;
  --dev: #000000;
}

body {
  background-color: var(--body);
}

body.dark {
  --black: #ffffff;
  --white: #292929;
  --body: #242424;
  --red: #ffffff;

  --font-gray: rgba(255, 255, 255, 0.5);
  --border-color: rgba(255, 255, 255, 0.05);

  --facebook: #ffffff;
  --codepen: #ffffff;
  --twitter: #ffffff;
  --github: #ffffff;
  --dev: #ffffff;
}

.main-container {
  max-width: 1600px;
  width: 100%;

  margin: auto;
}

.head-container {
  border-bottom: solid 1px var(--border-color);
  background-color: var(--white);

  height: 100px;
  display: flex;
}

.site-logo {
  display: grid;
  place-items: center left;

  padding: 0px 40px;

  height: 100%;
  width: 20%;
}

.site-logo div {
  height: 40px;

  display: flex;

  color: var(--primary-color);
}

.site-logo div span {
  height: 40px;
  width: 30px;

  display: grid;
  place-items: center;

  font-size: 30px;

  transform: scale(1.5);
}

.site-logo div p {
  display: inline-block;

  display: grid;
  place-items: center;
}

.site-logo img {
  height: 50px;
}

.site-logo img:last-child {
  display: none;
}

body.dark .site-logo img:first-child {
  display: none;
}

body.dark .site-logo img:last-child {
  display: block;
}

.site-navigation {
  height: 100%;
  width: 60%;
}

.site-navigation ul {
  list-style: none;

  display: flex;

  margin: auto;

  height: 100%;
  width: 60%;
}

.site-navigation ul li {
  margin: auto;
  padding: 10px;

  font-size: 13px;
  font-weight: bold;

  cursor: pointer;

  user-select: none;

  color: var(--black);

  text-transform: uppercase;
}

.site-navigation ul li:hover,
.site-navigation ul li.active {
  color: var(--primary-color);
}

.site-theme {
  height: 100%;
  width: 20%;

  padding: 0px 40px;

  display: grid;
  place-items: center right;

  user-select: none;
}

.toggle-theme-area {
  height: 40px;
  width: 100px;
}

.toggle-theme-area input[type="checkbox"] {
  display: none;
}

.toggle-theme-area label {
  height: 40px;
  width: 80px;

  display: inline-block;

  background-color: var(--white);

  border-radius: 50px;

  position: relative;

  overflow: hidden;

  cursor: pointer;

  border: solid 2px var(--border-color);

  padding: 4px;
}

.toggle-theme-area label i {
  height: 28px;
  width: 28px;

  transition: 0.3s ease-in-out;

  border-radius: 50%;

  position: relative;

  display: grid;
  place-items: center;
}

.toggle-theme-area label i svg {
  transition: 0.3s ease-in-out;

  position: absolute;

  height: 26px;
  width: 26px;
}

.toggle-theme-area label i svg:last-child {
  opacity: 0;
}

.toggle-theme-area input[type="checkbox"]:checked + label i {
  margin-left: 40px;
}

.toggle-theme-area input[type="checkbox"]:checked + label i svg {
  transform: rotate(180deg);
}

.toggle-theme-area input[type="checkbox"]:checked + label i svg:first-child {
  opacity: 0;
}

.toggle-theme-area input[type="checkbox"]:checked + label i svg:last-child {
  opacity: 1;
}

.profile-container {
  display: grid;
  place-items: center;

  height: 540px;
  width: 100%;
}

.profile {
  width: 500px;
}

.profile .photo {
  box-shadow: 0px 20px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 50%;

  overflow: hidden;

  height: 230px;
  width: 230px;

  margin: auto;
}

.profile .photo img {
  height: 100%;
  width: 100%;
}

.profile .full-name {
  text-align: center;
  padding: 30px 0px;
  width: 100%;
}

.profile .full-name h1 {
  color: var(--black);
  font-weight: lighter;
  font-size: 50px;
}

.profile .full-name h2 {
  color: var(--font-gray);

  font-weight: lighter;
  font-size: 25px;
}

.primary-box {
  width: 100%;
  height: 100%;

  padding: 30px;

  background-color: var(--white);
  box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.05);

  border-radius: 30px;
  border: solid 1px var(--border-color);
}

.primary-box h4 {
  text-transform: uppercase;

  padding-bottom: 20px;

  color: var(--black);

  font-size: 14px;
}

.primary-box h4 span {
  margin-left: 10px;

  font-weight: lighter;

  text-transform: capitalize;
}

.primary-box h4 a {
  color: var(--primary-color);
  text-decoration: none;
  float: right;
}

.primary-box p {
  color: var(--black);
  padding-bottom: 5px;
  font-size: 14px;
}

.primary-box ul {
  list-style: none;
  font-size: 12px;
}

.primary-box ul li {
  padding-bottom: 10px;
}

.primary-box ul li p {
  padding-bottom: 0px;
  font-weight: bold;
}

.primary-box ul li span {
  color: var(--font-gray);
}

.bio-container {
  padding: 0px 100px;
  display: flex;
  height: 350px;
  width: 100%;
}

.biography-container {
  padding: 20px;
  height: 100%;
  width: 70%;
}

.career-container {
  padding: 20px;
  width: 30%;
}

.career-container .career {
  width: 100%;
}

.career-container .social-media {
  padding-top: 40px;
  width: 100%;
}

.career-container .social-media ul {
  list-style: none;
  display: flex;
  height: 50px;
  width: 100%;
}

.career-container .social-media ul a {
  margin: auto;
}

.career-container .social-media ul li {
  box-shadow: 5px 5px 15px 0px rgba(0, 0, 0, 0.05);
  border: solid 1px var(--border-color);
  background-color: var(--white);
  border-radius: 50%;
  height: 40px;
  width: 40px;
  cursor: pointer;
  display: grid;
  place-items: center;
}

.career-container .social-media ul li svg {
  height: 18px;
  width: 18px;
}

.career-container .social-media ul li.facebook {
  fill: var(--facebook);
}

.career-container .social-media ul li.github {
  fill: var(--github);
}

.career-container .social-media ul li.twitter {
  fill: var(--twitter);
}

.career-container .social-media ul li.codepen {
  fill: var(--codepen);
}

.career-container .social-media ul li.dev {
  fill: var(--dev);
}

.snippet-container {
  padding: 40px 120px;
  width: 100%;
}

.snippet-box {
  width: 100%;
  display: flex;
}

.recent-snippet {
  height: 100%;
  width: 70%;
}

.snippet-primary {
  border: solid 1px var(--border-color);
  border-radius: 20px;
  overflow: hidden;
  width: 100%;
}

.snippet-primary .title {
  padding: 20px;
}

.snippet-primary .title p {
  font-size: 14px;
  font-weight: bold;
}

.snippet-primary .title a {
  color: var(--black);
  text-decoration: none;
  font-size: 14px;
}

.snippet-primary .title a:hover {
  color: var(--primary-color);
}

.snippet-primary .cover {
  border-top: solid 1px var(--border-color);
  border-bottom: solid 1px var(--border-color);
  overflow: hidden;
  height: 302px;
  width: 100%;
}

.snippet-primary .cover img {
  height: 100%;
  width: 100%;
}

.snippet-primary .languages {
  height: 60px;
  display: flex;
}

.snippet-primary .languages ul {
  padding-left: 15px;
  position: relative;
  list-style: none;
  display: flex;
  height: 100%;
  width: 100%;
}

.snippet-primary .languages ul li {
  border: solid 1px var(--border-color);
  border-radius: 20px;

  text-transform: uppercase;

  margin: auto 15px auto 0px;
  padding: 5px 10px;

  color: var(--black);

  font-weight: bold;
  font-size: 12px;

  cursor: pointer;
}

.snippet-primary .languages p {
  color: var(--black);
  display: grid;
  place-items: center;
  width: 150px;
  font-size: 15px;
}

.snippet-list {
  padding-left: 20px;
  height: 100%;
  width: 30%;
}

.snippet-secondary {
  border: solid 1px var(--border-color);
  border-radius: 20px;
  width: 100%;

  min-width: 100%;

  margin-bottom: 14.5px;
}

.snippet-secondary:last-child {
  margin: inherit;
}

.snippet-secondary .title {
  padding: 8px 12px;
}

.snippet-secondary .title p {
  padding-bottom: 0;
  font-size: 12px;
  font-weight: bold;
}

.snippet-secondary .title a {
  color: var(--black);
  text-decoration: none;
  font-size: 12px;
}

.snippet-secondary .title a:hover {
  color: var(--primary-color);
}

.snippet-secondary .languages ul {
  padding: 0px 10px 10px 10px;
  display: flex;
}

.snippet-secondary .languages ul li {
  border: solid 1px var(--border-color);
  border-radius: 20px;

  margin: auto 10px auto 0px;
  padding: 5px 10px;

  text-transform: uppercase;
  color: var(--black);

  font-weight: bold;
  font-size: 10px;

  cursor: pointer;
}

.snippet-secondary .languages ul li.html-tag:hover {
  background-color: #f53900;
  color: var(--white);
}

.snippet-secondary .languages ul li.css-tag:hover {
  background-color: #2965f1;
  color: var(--white);
}

.snippet-secondary .languages ul li.js-tag:hover {
  background-color: #f7df1e;
}

.snippet-primary .languages ul li.html-tag:hover {
  background-color: #f53900;
  color: var(--white);
}

.snippet-primary .languages ul li.css-tag:hover {
  background-color: #2965f1;
  color: var(--white);
}

.snippet-primary .languages ul li.js-tag:hover {
  background-color: #f7df1e;
}

.foot-container {
  width: 100%;

  padding: 20px;

  text-align: center;

  color: white;

  text-transform: capitalize;

  background-color: var(--primary-color);
}
