@media only screen and (max-width: 770px) {
  .head-container {
    height: 80px;
  }

  .site-logo {
    padding: 0px 20px;
    width: 100%;
  }

  .site-logo div span {
    transform: scale(1.2);
  }

  .site-logo div p {
    font-size: 15px;
  }

  .site-logo img {
    height: 40px;
  }

  .site-navigation {
    display: none;
  }

  .site-theme {
    width: 100%;
    padding: 0px 20px;
  }

  .profile-container {
    height: 400px;
  }

  .profile {
    width: auto;
  }

  .profile .photo {
    height: 150px;
    width: 150px;
  }

  .profile .full-name h1 {
    font-size: 30px;
  }

  .profile .full-name h2 {
    font-size: 18px;
  }

  .primary-box {
    padding: 20px;
  }

  .primary-box h4 {
    font-size: 12px;

    padding-bottom: 15px;
  }

  .primary-box p {
    font-size: 12px;
  }

  .bio-container {
    height: auto;

    padding: 0px 20px;

    display: block;
  }

  .biography-container {
    width: 100%;
    padding: 0;
  }

  .career-container {
    width: 100%;
    padding: 20px 0px;
  }

  .career-container .social-media {
    padding-top: 15px;
  }

  .snippet-container {
    padding: 0px 20px;

    height: auto;
  }

  .snippet-box {
    display: inline;
  }

  .recent-snippet {
    width: 100%;
  }

  .snippet-primary {
    width: 100%;
  }

  .snippet-primary .title {
    padding: 15px;
  }

  .snippet-primary .title p {
    font-size: 12px;
  }

  .snippet-primary .title a {
    font-size: 11px;
  }

  .snippet-primary .cover {
    height: 150px;
  }

  .snippet-primary .languages {
    height: 50px;
  }

  .snippet-primary .languages ul li {
    font-size: 10px;
    padding: 5px 10px;
  }

  .snippet-primary .languages p {
    padding: 0;
    font-size: 12px;
  }

  .snippet-list {
    padding: 20px 0px 0px 0px;

    width: 100%;
  }

  .foot-container {
    margin-top: 30px;

    font-size: 14px;
  }
}
